*
    box-sizing: border-box
    margin: 0
    padding: 0
    color: white
body
    display: flex
    align-items: center
    flex-direction: column
    width: 100%
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif
    height: 100vh
    background: #000011
.header
    &__h1
        padding: 10px 0px
.main
    border: 1px red solid
    max-width: 1440px
    display: flex
    justify-content: center
    min-width: 320px
    width: 50vw
    &__table
        border: 1px blue solid
        width: 100%
        &-row
            display: flex
        &-header,
        &-col
            border: 2px purple solid
            display: flex
            flex-direction: column
            align-items: center
            width: 50%
            overflow: auto
            overflow-x: hidden
            max-height: 80vh
        &-header
            padding: 10px 0px
        &-button
            all: unset
            cursor: pointer
            border-bottom: 1px white solid
        &-p,
        &-button
            padding: 20px 0px
            width: 100%
            text-align: center
            font-weight: bolder
            border-bottom: 1px white solid
            transition: 1s
            &:hover
                background: blue
